.Layer {
    width: 100vw;
    height: 100vh;

    background-size: contain;
    background-repeat: no-repeat;

    overflow: hidden;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.Layer.breath {
    -webkit-animation-name: breath;
            animation-name: breath;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes breath {
    0% {
        width: 100vw;
        height: 100vh;
        margin: 0;
    }
    40%, 60% {
        width: 105vw;
        height: 105vh;
        margin: -2.5vw 0 0 -2.5vh;
    }
    100% {
        width: 100vw;
        height: 100vh;
        margin: 0;
    }
}

@keyframes breath {
    0% {
        width: 100vw;
        height: 100vh;
        margin: 0;
    }
    40%, 60% {
        width: 105vw;
        height: 105vh;
        margin: -2.5vw 0 0 -2.5vh;
    }
    100% {
        width: 100vw;
        height: 100vh;
        margin: 0;
    }
}


